function ISWidget() {
    return { buildWithOptions };

    function buildWithOptions(options) {
        var body = document.getElementsByTagName("body")[0];
        var iframe = document.createElement("iframe");
        iframe.id = "ils-widget";
        iframe.style.height = "100%";
        iframe.style.width = "100%";
        iframe.style.position = "fixed";
        iframe.style.top = "0";
        iframe.style.left = "0";
        iframe.style.borderWidth = "0px";
        // iframe.src = "http://smartagwu.github.io/isw-widget";
        iframe.src = "http://localhost:3000/isw-widget";
        bindEvent(window, "message", (e) => getMessageFromIframe(e, iframe, body));
        iframe.onload = () => onLoadWidget(iframe, JSON.stringify(options));
        body.append(iframe);
    }

    function onLoadWidget(element, data){
        element.contentWindow.postMessage(data, "*");
    }

    function bindEvent(element, event, handler){
        if(element.addEventListener){
            element.addEventListener(event, handler, false);
        }else if(element.attachEvent){
            element.attachEvent("on" + event, handler);
        }
    }

    function getMessageFromIframe(e, iframe, body) {
        let data = e.data;
        if (data && data.type === "close") {
            body.removeChild(iframe);
        }
    }
}

module.exports = {
    ISWidget
}